.scroll-leaf-button {
  position: fixed;
  bottom: 80px;
  right: 30px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  z-index: 999;
  animation: fadeInUp 0.5s ease-out, floatSway 3s ease-in-out infinite;
}

.scroll-leaf-button img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

@keyframes floatSway {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  25% {
    transform: translateY(-4px) rotate(-3deg);
  }
  50% {
    transform: translateY(-8px) rotate(3deg);
  }
  75% {
    transform: translateY(-4px) rotate(-2deg);
  }
  100% {
    transform: translateY(0px) rotate(0deg);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
